<template>
  <div class="area" ref="area_scroll" v-if="cityInfo">
    <div class="scroll-wrap">
      <!--  -->
      <div class="hot-wrap city-list">
        <div class="title"></div>
        <ul class="hot-city">
          <li
            v-for="(item, index) in cityInfo.hotCities"
            :key="index"
            @click="$emit('selectCity',item)"
          >{{item.name}}</li>
        </ul>
      </div>
      <!--  -->
      <div class="city-wrap">
        <!-- key -->
        <div class="city-content city-list" v-for="(item, index) in keys" :key="index">
          <div class="title">{{item}}</div>
          <ul>
            <li
              @click="$emit('selectCity',city)"
              v-for="(city, index) in cityInfo.cities[item]"
              :key="index"
            >{{city.name}}</li>
          </ul>
        </div>
      </div>
    </div>
    <div class="keys-area">
      <ul>
        <li @click="selectKey(0)">#</li>
        <li @click="selectKey(index+1)" v-for="(item, index) in keys" :key="index">{{item}}</li>
      </ul>
    </div>
  </div>
</template>

<script>
import BScroll from 'better-scroll'
export default {
  name: 'alphabet',
  data () {
    return {
      scroll: null
    }
  },
  props: {
    cityInfo: Object,
    keys: Array
  },
  // mounted () {
  //   console.log(this.keys)
  //   console.log(this.cityInfo)
  // },
  methods: {
    initScroll () {
      this.scroll = new BScroll(this.$refs.area_scroll, {
        click: true
      })
    },
    selectKey (index) {
      const cityList = this.$refs.area_scroll.getElementsByClassName(
        'city-list'
      )
      let el = cityList[index]
      this.scroll.scrollToElement(el, 250)
    }
  }
}
</script>

<style scoped>
.area {
  margin-top: 10px;
  box-sizing: border-box;
  padding: 0 16px;
  background: #fff;
  height: calc(100% - 65px);
  /* overflow: hidden; */
}

.scroll-wrap {
  background: #fff;
  overflow: auto;
}

.title {
  color: #aaa;
  padding: 15px 0;
}

.hot-city {
  padding: 0 16px;
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
}

.hot-city li {
  width: 30%;
  background: #f1f1f1;
  margin: 0 10px 10px 0;
  text-align: center;
  padding: 10px;
  box-sizing: border-box;
}

.city-content li {
  padding: 10px;
  border-bottom: 1px solid #eee;
}

.keys-area {
  position: fixed;
  right: 0;
  top: 25%;
  color: #aaa;
  font-size: 12px;
  line-height: 1.4;
  text-align: center;
  padding: 0 5px;
}
</style>
