<template>
  <div class="city">
    <div class="search-wrap">
      <div class="search">
        <i class="fa fa-search"></i>
        <input v-model="cityVal" placeholder="" type="text">
      </div>
      <button @click="$router.push({name:'address',params:{city}})"></button>
    </div>
    <div style="height:100%" v-if="searchList.length==0">
      <div class="location">
        <location @click="selectCity({name:city})" :address="city"></location>
        <Alphabet @selectCity="selectCity" ref="allcity" :cityInfo="cityInfo" :keys="keys"/>
      </div>
    </div>
    <div class="search-list" v-else>
      <ul>
        <li @click="selectCity(item)" v-for="(item, index) in searchList" :key="index">{{item.name}}</li>
      </ul>
    </div>
  </div>
</template>

<script>
import Location from '../components/Location.vue'
import Alphabet from '../components/Alphabet.vue'
export default {
  name: 'city',
  components: {
    Location,
    Alphabet
  },
  data () {
    return {
      cityVal: '',
      cityInfo: null,
      keys: [],
      allCities: [],
      searchList: []
    }
  },
  computed: {
    city () {
      return (
        this.$store.getters.location.addressComponent.city ||
        this.$store.getters.location.addressComponent.province
      )
    }
  },
  created () {
    this.getCityInfo()
  },
  watch: {
    cityVal () {
      this.searchCity()
    }
  },
  methods: {
    getCityInfo () {
      this.$axios(
        'https://www.easy-mock.com/mock/5b574c739a5ff5320380791b/tuniu/citiesInfo'
      )
        .then(res => {
          console.log(res.data.data)
          this.cityInfo = res.data.data
          // key key
          this.keys = Object.keys(this.cityInfo.cities)
          // hotcitieskey
          // this.keys.shift()
          this.$nextTick(() => {
            this.$refs.allcity.initScroll()
          })
          this.keys.forEach(key => {
            this.cityInfo.cities[key].forEach(city => {
              this.allCities.push(city)
            })
          })
        })
        .catch(err => {
          console.log(err)
        })
    },
    selectCity (city) {
      this.$router.push({
        name: 'address',
        params: {
          city: city.name
        }
      })
    },
    searchCity () {
      if (!this.cityVal) {
        this.searchList = []
      } else {
        this.searchList = this.allCities.filter(item => {
          return item.name.indexOf(this.cityVal) !== -1
        })
      }
    }
  }
}
</script>

<style scoped>
.city {
  width: 100%;
  height: 100%;
  overflow: auto;
  box-sizing: border-box;
  padding-top: 45px;
}
.search-wrap {
  position: fixed;
  top: 0;
  height: 45px;
  width: 100%;
  background: #fff;
  box-sizing: border-box;
  padding: 3px 16px;
  display: flex;
  justify-content: space-between;
}
.search {
  background-color: #eee;
  border-radius: 10px;
  line-height: 40px;
  width: 85%;
  box-sizing: border-box;
  padding: 0 16px;
}
.search input {
  background-color: #eee;
  outline: none;
  border: none;
  margin-left: 10px;
}
.search-wrap button {
  outline: none;
  border: none;
  background-color: #fff;
  color: #009eef;
}
.location {
  background: #fff;
  padding: 8px 16px;
  height: 65px;
  box-sizing: border-box;
}
.search-list {
  background: #fff;
  padding: 5px 16px;
}

.search-list li {
  padding: 10px;
  border-bottom: 1px solid #eee;
}
</style>
